@use "../mixins" as *;
.homepage-top-slider{
	height:auto;
	background-color: var(--black);
	--slider-aspect-ratio: 1.25/1;
	@include bp(sm){
		--slider-aspect-ratio: 1.75/1;
	}
	@include bp(md){
		--slider-aspect-ratio: 8/3;
	}
}

.splide__slide {
	// min-width: 0;
	// flex: 0 0 auto;;
	// padding-block: 4rem;
}
.splide__slide > img {
	height: 100%;
	width: auto;
	flex: 1;
}
.splide, .splide__track, .splide__list	{
	height: 100%;
	width: 100%;
	flex: 0 0 auto;
	// width: max-content;

	// height: 100%;
}
.splide:not(.is-overflow) .splide__arrows {
	display: none;
  }
  .splide:not( .is-overflow ) .splide__list {
	justify-content: center;
  }
.slider {
	// height: 100%;
	aspect-ratio: var(--slider-aspect-ratio);
	// margin-left: calc(-1 * var(--spacer-8));
	// margin-right: calc(-1 * var(--spacer-8));
	// background: var(--color-primary-900);
	// background-image: url('/images/background-dark-gears.jpg');
	
	
	.splide__slide {
		aspect-ratio: var(--slider-aspect-ratio);
	background-size: cover;
	background-position: center;
	}

	.slide__overlay {
		// @include bp(md){
		position:absolute;
		top:0;
		left:0;
		right: 0;
		bottom:0;
		background: hsla(0,0%,20%, .8);
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		}
	// }
	.inner-slide {
		max-width: 1080px;
		width: 95%;
		margin-left: auto;
		margin-right: auto;
		--color-text: var(--color-secondary-100);
		color: var(--color-text);
		display: flex;
		flex-direction: column;
		gap: 1rem;
		align-items: center;
	}

	.splide__pagination {
		bottom: -1em;
	}
}