@use "./mixins" as *;

html {
	font-size: 16px;

	@include bp(md) {
		font-size: 18px;
	}
}

html,
body,
.site {
	height: 100%;
	scroll-padding: var(--scroll-padding);
	color: var(--color-text);
}

html * {
	// color: var(--color-text);
}

img {
	max-width: 100%;
	height: auto;
	// width: 100%;
}

ul.menu {
	padding: 0;
	margin: 0;
}

blockquote p:last-of-type {
	margin-bottom: 0;
}

body:not(.page-home):not(.single-summit) .site-content {
	// margin-top: var(--spacer-12);
}

.sponsor-logos__wrapper {
	.splide__slide {

		// height: 80px;
		align-items: center;
		display: flex;
	}
}

.border-radius-1 {
	border-radius: var(--radius-1);
}

.border-radius-2 {
	border-radius: var(--radius-2);
}

.border-radius-3 {
	border-radius: var(--radius-3);
}

.border-radius-4 {
	border-radius: var(--radius-4);
}

.geopin_header {
	position: fixed;
	z-index: 0;
	opacity: .05;
	transform: rotate(-30deg) translate(-6%, -7%);
	pointer-events: none;
}

.gear_body {
	position: absolute;
	z-index: 0;
	opacity: .5;
	pointer-events: none;
}

.splide__slide.who_attends_logos-gallery-slide {
	// height: 60px;
	display: flex;
	justify-content: center;
	align-items: center;

	img {
		width: 100%;
		height: 100%;
		object-fit: contain;
		// max-height: 60px;
	}
}

.media-text,
.container--single-column,
.flex-row.inner-container {
	position: relative;
}


// .single-testimonial,
// .testimonial-slider,
// .who_attends_logos__wrapper {
// 	padding-top: var(--section-spacer, var(--spacer-12));
// 	padding-bottom: var(--section-spacer, var(--spacer-12));
// }

.sidebar--default-theme {
	padding: 1rem;
	background: var(--color-primary-50);
	align-self: flex-start;
	border-radius: var(--radius-1);
}

.last-section {
	margin-bottom: calc(-1 * var(--footer-margin-top, var(--spacer-12)));
}

.pagination {
	display: flex;
	gap: .5rem;

	.page-numbers {
		border: 1px solid;
		border-radius: var(--radius-1);
		padding: 0 .5rem;
		display: flex;
		justify-content: center;

		&:hover,
		&.current {
			background: var(--color-text);
			color: var(--white);
		}
	}
}

#marketplace_sponsor_logos_wrap {
	position: relative;
}


.wp-block-gallery.wp-block-gallery-1 {
	gap: 10px !important;
}

.is-layout-flex {
	display: flex;
	flex-wrap: wrap;
}


.icon-grid__wrapper {
	position: relative;

	[class*=theme--] {
		// border: 1px solid #ccc;
		box-shadow: 0 2px 5px -2px #6661;
		;
	}
}

.page-become-a-sponsor {

	.icon-grid__wrapper {
		border-radius: var(--border-radius);
	}
}


.archive,
.blog {
	.hentry {
		margin-block: 2rem;
		clear: both;
		overflow: hidden;
		border-bottom: 1px solid #ccc;
		padding-block: 2rem 2rem;

		&:first-of-type {
			// padding-top: 0;
			margin-top: 0;
		}
	}

	a.post-thumbnail {
		float: left;
		width: min(50%, 300px);
		margin-right: 1rem;
	}

	.nav-links {
		display: flex;
		justify-content: space-evenly;
	}
}